	import listCell from '@/components/mix-list-cell';
	import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
    import {  
        mapState 
    } from 'vuex';  
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell,
			uniDrawer
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				showRigth:false,
				token:'',
				ss:false,
				sskeys:'',
				ssremen:[],
				sslishi:[],
				memberInfo:[]
			}
		},
		onLoad(){
			this.sslishi = uni.getStorageSync("cbe_sslishi");
		},
		onShow() {
			if(uni.getStorageSync('cbe_token')){
				this.token = uni.getStorageSync('cbe_token');
				this.getMemberInfo();
			}else{
				this.memberInfo = [];
			}
		},




















        computed: {
			...mapState(['hasLogin','userInfo'])
		},
        methods: {
			nnavaTo(url){
				uni.navigateTo({  
					url:url
				})
			},
			bss(e){
				this.sskeys = e.detail.value;
			},
			lianxi(){
				uni.showModal({
					title:"客服热线",
				    content: '400-004-5678',
					confirmColor:'#008AB0',
					confirmText:'现在拨打',
				    success: (e)=>{
				    	if(e.confirm){
							uni.makePhoneCall({
								phoneNumber: '400-004-5678'
							})
				    	}
				    }
				});
			},
			shanchu(e){
				var sslishi = uni.getStorageSync("cbe_sslishi");
				var newsslishi = [];
				var ii = 0;
				for(var i=0;i<sslishi.length;i++){
					if(sslishi[i]!=e){
						newsslishi[ii] = sslishi[i];
						ii++
					}
				}
				this.sslishi = newsslishi;
				uni.setStorageSync("cbe_sslishi",newsslishi);
			},
			quSousuo(){
				if(this.sskeys!=""){
					uni.navigateTo({
						url:'/pages/index/search?keywords='+this.sskeys
					})
				}
			},
			sousuo(){
				this.ss = true;
			},
			quxiaoss(e){
				if(e.detail.value==''){
					this.ss = true;
				}
			},
			navTo(url){
				if(!uni.getStorageSync('cbe_token')){
					url = '/pages/public/login';
				}
				uni.navigateTo({  
					url
				})  
			}, 
			navToa(url){
				uni.switchTab({
					url:url
				})
			},
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			},
			show(e) {
				if (e === 'left') {
					this.showLeft = true
				} else {
					this.showRigth = true
				}
			},
			hide() {
				this.showLeft = false
				this.showRigth = false
				this.ss = false;
			},
			closeDrawer(e){
				if (e === 'left') {
					this.showLeft = false
				} else {
					this.showRigth = false
				}
			},
			getMemberInfo(){
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/member/info',
					data: {token:uni.getStorageSync('cbe_token')},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (resa) => {
						var one = resa.data;
						if(one.code==0){
							one.one['phone'] = one.one.mobile.substr(0, 3) + '****' + one.one.mobile.substr(7);
							if(one.one.CNcardNo){
								one.one['CNcardNo'] = one.one.CNcardNo.replace(/^(.{4})(?:\d+)(.{4})$/,"$1******$2");
							}
							this.memberInfo = one.one;
							this.memberInfo.length = 11;
							// console.log(one.one);
						}else{
							this.memberInfo = [];
							uni.removeStorageSync('cbe_token');
						}
					},
					fail: (resa) => {
						this.getMemberInfo();
					}
				})
			}
        }  
    }  
